    margin-right: -5px
  &:nth-of-type(2)
    animation: neonsign 6s linear infinite 2s
  &:nth-of-type(3)
    animation: neonsign 6s linear infinite 3s
    transform: rotate(45deg)
    margin-left: -10px

@keyframes neonsign
  0%
    opacity: 1
  50%
    opacity: 0
  100%
    opacity: 0


</style>



